/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.header {
  padding: 25px 0 0 0;
  background: url("../img/header-bg.jpg");
  background-position-y: -165px;
  background-size: cover;
}

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__inner {
  display: flex;
  align-items: center;
}

.header__nav {
  background-color: #d24b46;
}

.header__tel {
  margin: 0 20px;
  font-size: 22px;
  color: #505aa0;
}

.header__text {
  font-size: 13px;
}

.header__link {
  font-size: 20px;
  color: #d24b46;
  text-transform: uppercase;
  text-decoration: underline;
}

.header__slogan {
  font-family: 'Marck Script', cursive;
  font-size: 26px;
  text-align: center;
  color: #d24b46;
}

.main-nav {
  padding: 20px 0;
  font-size: 18px;
  line-height: 1.2;
}

.main-nav__list {
  display: flex;
  justify-content: space-between;
}

.main-nav__level2 {
  display: none;
}

.main-nav__link {
  font-family: 'Roboto', sans-serif;
  color: #ffffff;
}

.main-nav__link:hover {
  border-bottom: 2px solid #ffffff;
}

.footer {
  padding: 40px 0;
  color: #ffffff;
  background-color: #505aa0;
}

.footer__layout {
  display: flex;
  justify-content: space-between;
}

.footer__tel {
  font-size: 18px;
  color: #ffffff;
  border-bottom: 2px solid #ffffff;
}

.features__layout {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.features__item {
  margin-bottom: 30px;
}

.feature {
  text-align: center;
  max-width: 300px;
}

.feature__icon {
  font-size: 70px;
}

.feature__icon svg {
  fill: #daa520;
}

.feature__title {
  font-size: 18px;
  color: grays;
}

.tabs__header {
  display: flex;
  justify-content: center;
}

.tabs__title {
  position: relative;
  margin-right: 30px;
  font-size: 16px;
  font-weight: 300;
  text-transform: uppercase;
  cursor: pointer;
}

.tabs__title:hover::before, .tabs__title.is-active::before {
  position: absolute;
  bottom: -3px;
  left: 0;
  right: 0;
  content: '';
  height: 1px;
  background-color: #0d0d0d;
}

.breadcrumbs--none {
  display: none;
}

.breadcrumbs__list {
  display: flex;
  padding: 10px 0;
}

.breadcrumbs__list li {
  margin-right: 25px;
}

.breadcrumbs__list a {
  font-size: 12px;
  color: #777777;
  text-transform: uppercase;
}

.section {
  position: relative;
  padding: 90px 0 0 0;
}

.section--red {
  background-color: #d24b46;
}

.section--blue {
  background-color: #505aa0;
}

.section__descr {
  display: flex;
  justify-content: space-around;
}

.section__link {
  display: flex;
  justify-content: center;
  margin-top: 45px;
}

.section__link a {
  font-size: 20px;
  color: #d24b46;
  text-transform: uppercase;
  border-bottom: 2px solid #ffffff;
  text-decoration: none;
}

.section__link a:hover {
  border-color: #d24b46;
}

.section__title {
  position: relative;
  display: flex;
  justify-content: center;
  max-width: 1000px;
  margin: 0 auto 45px;
  font-size: 20px;
  letter-spacing: 0.5px;
}

.section__title div {
  padding: 13px 25px 11px;
  background-color: #505aa0;
  color: #ffffff;
  font-weight: 400;
  text-transform: uppercase;
  border-radius: 40px;
}

.section__title::before, .section__title::after {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  content: '';
  width: 50%;
  height: 1px;
  background-color: #505aa0;
  z-index: -10;
}

.section__title::before {
  left: 0;
}

.section__title::after {
  right: 0;
}

.products__layout {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.social__item {
  margin-right: 10px;
}

.about-us {
  display: flex;
}

.about-us__content {
  margin-right: 20px;
}

.about-us__content p {
  margin-bottom: 20px;
}

.about-us__content p:last-child {
  margin-top: 40px;
}

.shipping-pay h3, .shipping-pay p, .shipping-pay ul li {
  line-height: 40px;
}

.shipping-pay ul li {
  padding-left: 5px;
}

.shipping-pay ul li:before {
  content: "-";
  position: relative;
  left: -5px;
}

.shipping-pay__shipping, .shipping-pay__pay {
  margin-bottom: 40px;
}

.contacts {
  position: absolute;
  top: 200px;
  right: 120px;
  width: 340px;
  min-height: 335px;
  padding-top: 40px;
  padding-bottom: 40px;
  text-align: center;
  border: 4px solid #ffffff;
  border-radius: 5px;
  margin: 20px 20px 230px 0;
  background-color: rgba(242, 234, 220, 0.9);
}

.contacts__icon svg {
  fill: #d24b46;
  width: 100px;
  height: 100px;
}

.contacts__info-title {
  text-transform: uppercase;
  color: #d24b46;
  margin-top: 30px;
}

.contacts__info-text {
  margin-top: 10px;
}

.block-ask {
  display: flex;
}

.block-ask__title {
  font-size: 50px;
  line-height: 50px;
  text-transform: uppercase;
}

.block-ask__title span {
  font-weight: bold;
  color: #d24b46;
}

.block-ask__link-popup {
  margin-left: 20px;
  font-size: 25px;
  line-height: 40px;
  text-decoration: underline;
}

.popup {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -250px;
  margin-left: -200px;
  padding-bottom: 30px;
  width: 400px;
  min-height: 315px;
  border: 1px solid #ffffff;
  background-color: #777777;
  z-index: 5;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  background-color: #000000;
  opacity: 0.8;
  cursor: pointer;
  z-index: 3;
}

.form {
  position: relative;
}

.form__title {
  padding: 10px;
  font-weight: normal;
  color: #ffffff;
  background-color: #505aa0;
}

.form input, .form textarea {
  display: block;
  width: 95%;
  padding: 7px;
  margin-top: 10px;
  margin-right: 10px;
  margin-bottom: 0px;
  margin-left: 10px;
  border: 1px solid #CCCCCC;
  background-color: #fff;
}

.form textarea {
  min-height: 90px;
  margin-bottom: 30px;
}

.btn--form {
  float: right;
  padding: 7px  20px;
  margin-right: 10px;
  font-weight: bold;
  color: #505aa0;
  border: 1px solid #d24b46;
  border-radius: 5px;
  background-color: #daa520;
}

.btn-close {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px;
  font-size: 0;
  background-color: #505aa0;
  opacity: 0.5;
}

.btn-close:hover {
  opacity: 1;
}

.btn-close:active {
  opacity: 0.1;
}

.btn-close:before, .btn-close:after {
  content: "";
  position: absolute;
  top: 7px;
  right: -3px;
  width: 22px;
  height: 4px;
  background-color: #d24b46;
  border-radius: 2px;
}

.btn-close:before {
  transform: rotate(45deg);
}

.btn-close:after {
  transform: rotate(-45deg);
}
