.form {
  position: relative;

  &__title {
    padding: 10px;

    font-weight: normal;
    color: $white;
    background-color: #505aa0; }

  input, textarea {
    display: block;

    width: 95%;

    padding: 7px;
    margin-top: 10px;
    margin-right: 10px;
    margin-bottom: 0px;
    margin-left: 10px;
    border: 1px solid #CCCCCC;
    background-color: #fff; }

  textarea {
    min-height: 90px;
    margin-bottom: 30px; } }

.btn {

  &--form {
    float: right;

    padding: 7px  20px;
    margin-right: 10px;
    font-weight: bold;
    color: $blue;
    border: 1px solid $red;
    border-radius: 5px;
    background-color: $gold; }

  &-close {
    position: absolute;
    top: 10px;
    right: 10px;

    width: 20px;
    height: 20px;
    font-size: 0;

    background-color: $blue;
    opacity: 0.5;

    &:hover {
      opacity: 1; }

    &:active {
      opacity: 0.1; }

    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 7px;
      right: -3px;

      width: 22px;
      height: 4px;

      background-color: $red;
      border-radius: 2px; }

    &:before {
      transform: rotate(45deg); }

    &:after {
      transform: rotate(-45deg); } } }
